import { Box, Button, CircularProgress, Dialog, DialogActions, DialogContent, DialogTitle, Grid, InputAdornment, makeStyles, TextField, Typography } from '@material-ui/core';
import { Autocomplete } from '@material-ui/lab';
import _ from 'lodash';
import React, { Fragment, useEffect, useState } from 'react';
import { menuUpdateHandler } from '../../axiosCalls/Menu/menuUpdate';
import APP_CONSTANTS from '../../common/constants/APP_CONSTANTS';
import extractIDFromObjectArray from '../../common/functions/extractIDFromObjectArray';
import tagIDToTagObject from '../../common/functions/tagIDtoTagObject';

const useStyles = makeStyles((theme) => ({
  root: {},
  spinner: {
    color:"white",
  }
}))

function MenuDialogEdit(props) {
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const [name, setName] = useState("");
  const [imgURL, setImgURL] = useState("");
  const [price, setPrice] = useState("");
  const [size, setSize] = useState(1);
  const [tags, setTags] = useState([]);
  const [recommendation, setRecommendation] = useState("Bakmi Ayam");
  const [running, setRunning] = useState(false);
  
  let {tagsDictionary, menuObject, setters} = props;
  
  useEffect(() => {
    setRecommendation(APP_CONSTANTS.MENU_SUGGESTIONS[_.random(0, APP_CONSTANTS.MENU_SUGGESTIONS.length - 1)]);
    setName(menuObject?.name || "");
    setImgURL(menuObject?.imageURL || "");
    setPrice(menuObject?.price || 0);
    setSize(menuObject?.servingSize || 1);
    setTags(Array.isArray(menuObject?.tags) ? menuObject?.tags : []);
  }, [menuObject, open])
  
  const handleOpen = (state) => {
    setOpen(state);
  }

  return (
    <Fragment>
      <Button variant="contained" color="primary" onClick={function() {handleOpen(true)}} size="small">
        <Typography variant="caption">Edit</Typography>
      </Button>
      <Dialog open={open} onClose={function() {handleOpen(false)}}>
        <DialogTitle>
          <Box p={2}>
            Edit {name || "Menu Item"}
          </Box>
        </DialogTitle>
        <DialogContent>
          <Box px={2} mb={4}>
            <TextField label="Nama Item" type="text" size="small" fullWidth
              placeholder={recommendation}
              InputLabelProps={{shrink: true}}
              helperText="Ubah nama makanan / minuman yang disediakan oleh restoran"
              defaultValue={name}
              onChange={(event) => {setName(event.target.value)}}
            />
          </Box>

          <Box px={2} mb={4}>
            <TextField label="Image URL" type="text" size="small" fullWidth
              InputLabelProps={{shrink: true}}
              helperText="URL / Link gambar menu item yang akan ditampilkan"
              defaultValue={imgURL}
              onChange={(event) => {setImgURL(event.target.value)}}
            />
          </Box>

          <Box px={2} mb={4}>
            <Grid container spacing={2}>
              <Grid item xs={12} md={6}>
                <TextField label="Harga Item" type="text" size="small"
                  onChange={(event) => {setPrice(event.target.value)}}
                  defaultValue={price}
                  InputProps={{
                    startAdornment: <InputAdornment position="start">Rp</InputAdornment>
                  }}
                />
              </Grid>
              <Grid item xs={12} md={6}>
                <TextField label="Serving Size" type="text" size="small"
                  InputLabelProps={{shrink: true}}
                  onChange={(event) => {setSize(event.target.value)}}
                  defaultValue={size}
                  InputProps={{
                    endAdornment: <InputAdornment position="end">orang</InputAdornment>
                  }}
                />
              </Grid>
            </Grid>
          </Box>

          <Box px={2} mb={4}>
            <Autocomplete multiple
              options={(tagsDictionary?.length !== 0 ? tagsDictionary : [])}
              onChange={(event, inputTags) => {setTags(extractIDFromObjectArray(inputTags))}}
              getOptionLabel={(tag) => tag?.namaKategori}
              defaultValue={tags.map(tag => tagIDToTagObject(tag, tagsDictionary))}
              renderInput={(params) => (
                <TextField {...params} size="small" label="Tags" fullWidth
                  InputLabelProps={{shrink: true}}
                  placeholder="Pilih Kategori..."
                  helperText="Tambahkan kategori menu makanan / minuman itu"
                />
              )}
            />
          </Box>
          
        </DialogContent>
        <DialogActions>
          <Box p={2} display="flex" flexDirection="row">
            <Box mr={2}>
              <Button onClick={function() {handleOpen(false)}} color="primary">
                Cancel
              </Button>
            </Box>
            <Button 
              onClick={
                function(event) {
                  handleOpen(false);
                  (menuUpdateHandler(
                    {
                      id: menuObject._id,
                      name: name,
                      imageURL: imgURL,
                      price: price,
                      servingSize: size,
                      tags: tags
                    },
                    setRunning,
                    setters.setMenu
                  ))(event);
                }
              } 
              color="primary" 
              variant="contained"
            >
              {!running ? "Save Changes" : <CircularProgress className={classes.spinner} size={14}/>}
            </Button>
          </Box>
        </DialogActions>
      </Dialog>
    </Fragment>
  )
}

export default MenuDialogEdit;