import qs from 'qs';
import { toast } from 'react-toastify';
import toastConfig from '../../common/constants/toastConfig';
import menuGetAllHandler from './menuGetAll';

var axios = require('axios');
const { default: APP_CONSTANTS } = require('../../common/constants/APP_CONSTANTS');

function menuDelete(successFunction, errorFunction, id) {
  axios({
    method: 'delete',
    url: `${APP_CONSTANTS.API_URL}/menus`,
    headers: { 
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    data : qs.stringify({id: id})
  })
  .then(successFunction)
  .catch(errorFunction);
}

function menuDeleteHandler(id, throttleHandler = null, menuSetter = null) {
  return function (event = null) {
    console.log("MenuDelete")
    if (throttleHandler !== null) {throttleHandler(true);}
    menuDelete(
      function(res) {
        // Refresh Menu
        if (menuSetter !== null) {
          (menuGetAllHandler(menuSetter))()
        }

        // Throttler Handler
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.success("Menu berhasil dihapus", toastConfig)
        }
      },
      function(err) {
        console.log(err);
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.error("Maaf, tidak bisa menghapus data. Terjadi kesalahan pada server...", toastConfig)
        }
      },
      id
    )
  }
}

export {menuDelete, menuDeleteHandler};