import { Box, Card, CardContent, CardMedia, Chip, makeStyles, Typography, Tooltip } from "@material-ui/core";
import React, { Fragment } from "react";
import _ from "lodash";
import currencyFormat from "../common/functions/currencyFormat";
import APP_CONSTANTS from "../common/constants/APP_CONSTANTS";
import tagIDToName from "../common/functions/tagIDToName";

const useStyles = makeStyles((theme) => ({
  root: {
    maxWidth: 340,
  },
  media: {
    height: 200
  },
  tagChip: {
    margin: theme.spacing(0.5)
  }
}))

function WaiterRecommendationCard(props) {
  const classes = useStyles();
  const {menuObject, tagsDictionary} = props;

  return (
    <Fragment>
      {
      _.isObject(menuObject)
      ? <Card className={classes.root}>
          <CardMedia
            className={classes.media}
            image={menuObject.imageURL || "https://ak.picdn.net/shutterstock/videos/30581293/thumb/1.jpg?ip=x480"}
            title={menuObject.name || "Menu"}
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="h2">
              {menuObject.name}
            </Typography>
            <Typography gutterBottom variant="caption" component="p">
              Price: Rp{currencyFormat(menuObject.price)}
            </Typography>
            <Typography gutterBottom variant="caption" component="p">
              Serving Size: {menuObject.servingSize}
            </Typography>
            {
              menuObject.tags?.length > 0
              ? <Box mt={2}>
                  {menuObject.tags.slice(0, APP_CONSTANTS.MAX_TAGS_RENDERED).map((tag, index) => {
                    let tagName = tagIDToName(tag, tagsDictionary);
                    if (tagName !== null) {
                      return (
                        <Chip key={index} label={tagName} variant="outlined" className={classes.tagChip}/>
                      )
                    } else return null
                  })}
                  {
                    menuObject.tags.length > APP_CONSTANTS.MAX_TAGS_RENDERED
                    ? <Tooltip title={menuObject.tags.slice(APP_CONSTANTS.MAX_TAGS_RENDERED).map(tag => tagIDToName(tag, tagsDictionary)).join(", ")}>
                        <Chip label="..." variant="outlined" className={classes.tagChip}/>
                      </Tooltip>
                    : null
                  }
                </Box>
              : null
            }
          </CardContent>
        </Card>
      : null
    }
    </Fragment>
  )
}

export default WaiterRecommendationCard;