import { Box, Button, CircularProgress, Dialog, DialogActions, DialogContent, DialogTitle, makeStyles, TextField, Typography } from '@material-ui/core';
import _ from 'lodash';
import React, { Fragment, useEffect, useState } from 'react';
import { tagsPostHandler } from '../../axiosCalls/Tags/tagsPost';
import APP_CONSTANTS from '../../common/constants/APP_CONSTANTS';

const useStyles = makeStyles((theme) => ({
  root: {},
  spinner: {
    color:"white",
  }
}))

function TagsDialogAdd(props) {
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const [tagItemRecommendation, setTagItemRecommendation] = useState("makanan");
  const [tagItemInput, setTagItemInput] = useState("");
  const [running, setRunning] = useState(false);
  const [validation, setValidation] = useState(false);
  
  let {tagsDictionary, setters} = props;

  useEffect(() => {    
    setTagItemRecommendation(APP_CONSTANTS.TAGS_SUGGESTIONS[_.random(0, APP_CONSTANTS.TAGS_SUGGESTIONS.length - 1)])
  }, [])

  useEffect(() => {
    let temp = tagsDictionary.map(tag => tag.namaKategori)
    let valTemp = temp.findIndex(tag => tag === tagItemInput.toLowerCase())
    setValidation(valTemp !== -1)
  }, [tagItemInput, tagsDictionary])
  
  const handleOpen = (state) => {
    setOpen(state);
  }

  return (
    <Fragment>
      <Button variant="contained" color="primary" onClick={function() {handleOpen(true)}} size="small">
        <Typography variant="caption">Add Item</Typography>
      </Button>
      <Dialog open={open} onClose={function() {handleOpen(false)}}>
        <DialogTitle>
          <Box p={2}>
            Add Tag Item
          </Box>
        </DialogTitle>
        <DialogContent>
          <Box px={2} mb={4}>
            <TextField label="Insert Tag Name" type="text" size="small"
              error={validation}
              placeholder={tagItemRecommendation}
              InputLabelProps={{shrink: true}}
              helperText={
                validation
                ? `Maaf, kategori ${tagItemInput} sudah ada di daftar kategori / tags...`
                : "Tambahkan sebuah tag (kategori) yang mendeskripsikan sifat makanan / minuman."
              }
              onChange={(event) => {
                setTagItemInput(event.target.value.toLowerCase());
              }}
              
            />
          </Box>
        </DialogContent>
        <DialogActions>
          <Box p={2} display="flex" flexDirection="row">
            <Box mr={2}>
              <Button onClick={function() {handleOpen(false)}} color="primary">
                Cancel
              </Button>
            </Box>
            <Button
              disabled={validation} 
              onClick={
                function(event) {
                  handleOpen(false);
                  (tagsPostHandler({
                    namaKategori: tagItemInput
                  },
                  setRunning,
                  setters.setTagsDictionary
                  ))(event);
                  setTagItemInput("");
                }} 
              color="primary" 
              variant="contained"
            >
              {!running ? "Add a Tag" : <CircularProgress className={classes.spinner} size={14}/>}
            </Button>
          </Box>
        </DialogActions>
      </Dialog>
    </Fragment>
  )
}

export default TagsDialogAdd;