import React, { Fragment, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import { Drawer, Hidden, IconButton, SwipeableDrawer, Typography } from '@material-ui/core';
import MenuIcon from '@material-ui/icons/Menu';
import { NavLink } from 'react-router-dom';
import APP_ROUTES from './constants/APP_ROUTES';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  appBar: {
    zIndex: theme.zIndex.drawer + 1,
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
  },
  drawerContainer: {
    overflow: 'auto',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
  },

  layoutlistitem: {
    "&.active": {
      "& .drawer-list-text": {
        color: theme.palette.secondary.dark,
        fontWeight: "bolder"
      },

      "& .drawer-list-icon": {
        color: theme.palette.secondary.dark,
      },
    },
  },
}));

export default function Layout(props) {
  const classes = useStyles();
  const [openDrawer, setOpenDrawer] = useState(false);
  const toggleDrawer = (open) => (event) => {
    if (event && event.type === 'keydown' && (event.key === 'Tab' || event.key === 'Shift')) {
      return;
    }

    setOpenDrawer(open);
  }

  const drawer = (
    <Fragment>
      <Toolbar />
      <div className={classes.drawerContainer}>
        <List>
          {APP_ROUTES.map((text, index) => (
            <ListItem button key={index} component={NavLink} to={text[2]} className={classes.layoutlistitem}>
              <ListItemIcon>
                {text[0]}
              </ListItemIcon>
              <ListItemText>
                <Typography className="drawer-list-text" variant="body2">{text[1]}</Typography>
              </ListItemText>
            </ListItem>
          ))}
        </List>
      </div>
    </Fragment>
  )

  return (
    <div className={classes.root}>
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <div className={classes.navbar_links_wrapper}>
            <Hidden mdUp>
              <IconButton edge="start" color="inherit" aria-label="menu" onClick={toggleDrawer(true)}>
                <MenuIcon />
              </IconButton>
            </Hidden>
          </div>
          <Typography>
            Recommendation Management
          </Typography>
        </Toolbar>
      </AppBar>

      <Hidden smDown>
        <Drawer
          className={classes.drawer}
          classes={{
            paper: classes.drawerPaper,
          }}
          anchor="left"
          variant="permanent"
        >
          {drawer}
        </Drawer>
      </Hidden>

      <SwipeableDrawer
        className={classes.drawer}
        classes={{
          paper: classes.drawerPaper,
        }}
        anchor="left"
        open={openDrawer}
        onClose={toggleDrawer(false)}
        onOpen={toggleDrawer(true)}
      >
        {drawer}
      </SwipeableDrawer>

      <main className={classes.content}>
        <Toolbar />
        {props.children}
      </main>
    </div>
  );
}
