import qs from 'qs';
import { toast } from 'react-toastify';
import toastConfig from '../../common/constants/toastConfig';
import tagsGetAllHandler from './tagsGetAll';

var axios = require('axios');
const { default: APP_CONSTANTS } = require('../../common/constants/APP_CONSTANTS');

function tagsPost(successFunction, errorFunction, data) {
  axios({
    method: 'post',
    url: `${APP_CONSTANTS.API_URL}/tags`,
    headers: { 
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    data : qs.stringify(data)
  })
  .then(successFunction)
  .catch(errorFunction);
}

function tagsPostHandler(data, throttleHandler = null, tagsSetter = null) {
  return function(event = null) {
    console.log("TagsPostHandler");
    console.log(data);
    if (throttleHandler !== null) {throttleHandler(true);}
    tagsPost(
      function(res) {
        // Refresh Tags
        if (tagsSetter !== null) {
          (tagsGetAllHandler(tagsSetter))()
        }

        // Throttler Handler
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.success(`Berhasil menamabahkan ${data.namaKategori} pada daftar kategori / tags`, toastConfig)
        }
      },
      function(err) {
        console.log(err);
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.error("Maaf, tidak bisa menambahkan data. Terjadi kesalahan pada server...", toastConfig)
        }
      },
      data
    )
  }
}

export {tagsPost, tagsPostHandler};
