import React, { useState } from 'react';
import { Box, Chip, makeStyles, Paper, Table, TableBody, TableCell, TableContainer, TableHead, TablePagination, TableRow, Toolbar, Tooltip, Typography } from "@material-ui/core";
import currencyFormat from '../common/functions/currencyFormat';
import APP_CONSTANTS from '../common/constants/APP_CONSTANTS';
import tagIDToName from "../common/functions/tagIDToName";

const useStyles = makeStyles((theme) => ({
  root: {
    width: "100%"
  },
  paper: {
    width: "100%",
    marginBottom: theme.spacing(2),
  },
  tableRow: {
    cursor: "pointer",
  },
  tagChip: {
    margin: theme.spacing(0.5)
  }
}))

function WaiterRecommendationTable(props) {
  const {tagsDictionary, setters, filteredMenu} = props;
  const classes = useStyles();

  // Table Pagination
  const [rowsPerPage, setRowsPerPage] = useState(5);
  const [page, setPage] = useState(0);

  const handleChangePage = (event, newPage) => {
    setPage(newPage);
  };

  const handleChangeRowsPerPage = (event) => {
    setRowsPerPage(parseInt(event.target.value, 10));
    setPage(0);
  };

  return (
    <Box className={classes.root}>
      <Paper className={classes.paper}>
        <Toolbar>
          <Typography variant="body1" id="tableTitle" component="div">
            <b>Results</b>
          </Typography>
        </Toolbar>
        <TableContainer>
          <Table size="small">
            <TableHead>
              <TableRow>
                <TableCell>Nama</TableCell>
                <TableCell>Tags</TableCell>
                <TableCell padding="checkbox">Harga</TableCell>
                <TableCell padding="checkbox">Size</TableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {
                (
                  rowsPerPage > 0
                  ? filteredMenu.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage)
                  : filteredMenu
                ).map((item, index) => (
                  <TableRow
                    hover
                    className={classes.tableRow}
                    onClick={() => {setters.setTableIndex(index)}}
                    key={index}
                  >
                    <TableCell>{item.name}</TableCell>
                    <TableCell>
                      {
                        item.tags.slice(0, APP_CONSTANTS.MAX_TAGS_RENDERED).map((tag, index) => {
                          let tagName = tagIDToName(tag, tagsDictionary)
                          if (tagName !== null) {
                            return (
                              <Chip key={index} label={tagName} variant="outlined" className={classes.tagChip}/>
                            )
                          } else return null
                        })
                      }
                      {
                        item.tags.length > APP_CONSTANTS.MAX_TAGS_RENDERED
                        ? <Tooltip title={item.tags.slice(APP_CONSTANTS.MAX_TAGS_RENDERED).map(tag => tagIDToName(tag, tagsDictionary)).join(", ")} placement="bottom">
                            <Chip label="..." variant="outlined" className={classes.tagChip}/>
                          </Tooltip>
                        : null
                      }
                    </TableCell>
                    <TableCell padding="checkbox">Rp{currencyFormat(item.price)}</TableCell>
                    <TableCell padding="checkbox">{item.servingSize}</TableCell>
                  </TableRow>
                ))
              }
            </TableBody>
          </Table>
        </TableContainer>
        <TablePagination
          rowsPerPageOptions={[5, 10, 25, {label: 'All', value: -1}]}
          component="div"
          count={filteredMenu.length}
          rowsPerPage={rowsPerPage}
          page={page}
          onPageChange={handleChangePage}
          onRowsPerPageChange={handleChangeRowsPerPage}
        />
      </Paper>
    </Box>
  )
}

export default WaiterRecommendationTable;