import qs from 'qs';
import { toast } from 'react-toastify';
import toastConfig from '../../common/constants/toastConfig';
import tagsGetAllHandler from './tagsGetAll';

var axios = require('axios');
const { default: APP_CONSTANTS } = require('../../common/constants/APP_CONSTANTS');

function tagsUpdate(successFunction, errorFunction, data) {
  axios({
    method: 'put',
    url: `${APP_CONSTANTS.API_URL}/tags`,
    headers: { 
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    data : qs.stringify(data)
  })
  .then(successFunction)
  .catch(errorFunction);
}

function tagsUpdateHandler(data, throttleHandler = null, tagsSetter = null) {
  return function(event = null) {
    console.log("tagsUpdateHandler");
    if (throttleHandler !== null) {throttleHandler(true);}
    tagsUpdate(
      function(res) {
        // Refresh Tag
        if (tagsSetter !== null) {
          (tagsGetAllHandler(tagsSetter))()
        }

        // Throttler Handler
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.success(`Berhasil mengubah kategori ${data.name}`, toastConfig)
        }
      },
      function(err) {
        console.log(err);
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.error("Maaf, tidak bisa mengubah kategori / tag. Terjadi kesalahan pada server...", toastConfig)
        }
      },
      data
    )
  }
}

export {tagsUpdate, tagsUpdateHandler};
