// tagID = input tag ID
// tagsDictionary = tags dictionary from the server

function tagIDToName(tagID, tagsDictionary) {
  let idx = tagsDictionary.findIndex(elmt => {return tagID === elmt._id})
  if (idx !== -1) {
    return tagsDictionary[idx].namaKategori
  } else return null;
}

export default tagIDToName;