import { Button, Box, Grid, Typography, makeStyles, Container, TableContainer, Paper, Table, TableHead, TableRow, TableCell, TextField, CircularProgress } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import Layout from '../../common/Layout';
import TagsDialogAdd from './_TagsDialogAdd';
import TagsTableBody from './_TagsTableBody';
import tagsGetAllHandler from '../../axiosCalls/Tags/tagsGetAll';

const useStyles = makeStyles((theme) => ({
  root: {},
  spinner: {
    color:"white",
  }
}))

function TagsMainPage(props) {
  const [tagsDictionary, setTagsDictionary] = useState([]);
  const [search, setSearch] = useState("");
  const [running, setRunning] = useState(false);

  const classes = useStyles();

  useEffect(() => {
    // effect
    // Get Data
    (async function () {
      await tagsGetAllHandler(setTagsDictionary, setRunning)();
    })();
  }, [])

  return (
    <Layout>
      <Container>
        <Grid container spacing={2}>
          <Grid item xs={9}>
            <Typography variant="h5" className={classes.root}>
              Tags Management
            </Typography>
          </Grid>
          <Grid item xs={3}>
            <Box display="flex" justifyContent="flex-end">
              <Button disabled={running} size="small" color="primary" variant="contained" onClick={tagsGetAllHandler(setTagsDictionary, setRunning)}>
                <Typography variant="caption">
                  {!running ? "Refresh" : <CircularProgress className={classes.spinner} size={12}/>}
                </Typography>
              </Button>
            </Box>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography variant="body1">
              Disini, Anda dapat mengatur kategori-kategori makanan yang akan muncul saat menambahkan item menu pada Menu Management. 
              Kategori-kategori ini digunakan untuk memberikan karakteristik pada item menu makanan atau minuman agar waiter dapat 
              merekomendasikan menu sesuai dengan preferensi dan pantangan customer.
            </Typography>
          </Grid>
        </Grid>

        <Box mt={8}>
          <Grid container>
            <Grid item xs={12} md={6}>
              <Typography variant="h6">
                Manage Tags
              </Typography>
              <Box mt={1}>
                <TextField label="Search Tag by Name" type="text" size="small" fullWidth
                  onChange={(event) => {setSearch(event.target.value)}}
                />
              </Box>
            </Grid>
          </Grid>
        </Box>

        <Box mt={3}>
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell>Tag Name</TableCell>
                  <TableCell align="right">
                    <TagsDialogAdd 
                      tagsDictionary={tagsDictionary}
                      setters={{ 
                        setTagsDictionary,
                        throttleHandler: setRunning
                      }}
                    />
                  </TableCell>
                </TableRow>
              </TableHead>
              <TagsTableBody 
                tagsDictionary={tagsDictionary} 
                tagsFilter={search}
                setters={{
                  setTagsDictionary,
                  throttleHandler: setRunning
                }}
              />
            </Table>
          </TableContainer>
        </Box>
      </Container>
    </Layout>
  )
}

export default TagsMainPage;