import React, { useState, useEffect } from 'react';
import { makeStyles, TableRow, TableCell, TableBody, Chip, Tooltip } from '@material-ui/core';
import currencyFormat from '../../common/functions/currencyFormat';
import tagIDToName from '../../common/functions/tagIDToName';
import APP_CONSTANTS from '../../common/constants/APP_CONSTANTS';
import MenuDialogEdit from './_MenuDialogEdit';
import MenuDialogDelete from './_MenuDialogDelete';

const useStyles = makeStyles((theme) => ({
  root: {},
  tableImage: {
    width: "64px",
    height: "64px",
  },
  tagchip: {
    margin: "4px"
  },
  rowcell: {
    maxWidth: "300px"
  }
}))

function MenuTableBody(props) {
  const [filteredMenu, setFilteredMenu] = useState([]);

  const {menu, tagsDictionary, menuFilter, setters} = props;

  const classes = useStyles();

  useEffect(() => {
    let temp = menu;
    let tempFilter = menuFilter.toLowerCase();

    if (!tempFilter) {
      setFilteredMenu(menu)
    }

    if (tempFilter) {
      temp = temp.filter(item => item.name.toLowerCase().indexOf(tempFilter) > -1);
      setFilteredMenu(temp);
    }

    console.log(menu);
  }, [menu, menuFilter])

  return (
    <TableBody>
      {
        filteredMenu.length > 0
        ? <>
          {filteredMenu.map((row, index) => (
            <TableRow key={index}>
              <TableCell size="small" padding="checkbox">
                {row.imageURL 
                ? <a href={row.imageURL} target="_blank" rel="noreferrer noopener">
                    <img src={row.imageURL} className={classes.tableImage} alt=""/>
                  </a>
                : null}
              </TableCell>
              <TableCell>{row.name}</TableCell>
              <TableCell className={classes.rowcell}>
                {row.tags.slice(0, APP_CONSTANTS.MAX_TAGS_RENDERED).map((tag, index) => {
                  let tagName = tagIDToName(tag, tagsDictionary);
                  if (tagName !== null) {
                    return (
                      <Chip key={index} label={tagName} variant="outlined" className={classes.tagchip}/>
                    )
                  } else return null
                })}
                {row.tags.length > APP_CONSTANTS.MAX_TAGS_RENDERED
                ? <Tooltip title={row.tags.slice(APP_CONSTANTS.MAX_TAGS_RENDERED).map(tag => {return tagIDToName(tag, tagsDictionary)}).join(", ")} placement="bottom">
                    <Chip label="..." variant="outlined" className={classes.tagchip}/> 
                  </Tooltip>
                : null}
              </TableCell>
              <TableCell>{currencyFormat(row.price)}</TableCell>
              <TableCell>{row.servingSize}</TableCell>
              <TableCell align="right">
                <MenuDialogEdit tagsDictionary={tagsDictionary} menuObject={row} setters={setters}/>
                <MenuDialogDelete menuObject={row} setters={setters}/>
              </TableCell>
            </TableRow>
          ))}
          </>
        : <TableRow>
            <TableCell colSpan={6} align="center">Tidak ada data makanan</TableCell>
          </TableRow> 
      }
    </TableBody>
  )
}

export default MenuTableBody