import { Box, Container, Grid, makeStyles, Typography, Button, TextField, CircularProgress } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import Layout from '../common/Layout';
import _ from "lodash";
import tagsGetAllHandler from "../axiosCalls/Tags/tagsGetAll";
import menuGetAllHandler from "../axiosCalls/Menu/menuGetAll";
import WaiterRecommendationCard from './_WaiterRecommendationCard';
import { Autocomplete } from '@material-ui/lab';
import extractIDFromObjectArray from '../common/functions/extractIDFromObjectArray';
import WaiterRecommendationTable from './_WaiterRecommendationTable';

const useStyles = makeStyles((theme) => ({
  root: {},
  spinner: {
    color:"white",
  }
}))

function WaiterMainPage(props) {
  const classes = useStyles();

  // Fetch Data From DB 
  const [tagsDictionary, setTagsDictionary] = useState([]);
  const [menu, setMenu] = useState([]);

  // Controls Flow to disable throttling
  const [running, setRunning] = useState(false); 

  // Input: Tag Exclusion / Inclusion Filter
  const [exclude, setExclude] = useState([]);
  const [include, setInclude] = useState([]);

  // Output
  const [filteredMenu, setFilteredMenu] = useState([]);
  const [recommendedMenu, setRecommendedMenu] = useState(null);

  // Table Selection Handler
  const [tableIndex, setTableIndex] = useState(null);

  useEffect(() => {
    (tagsGetAllHandler(setTagsDictionary))();
    (menuGetAllHandler(setMenu))();
    
  }, []);

  useEffect(() => {
    if (filteredMenu.length > 0) {
      setRecommendedMenu(filteredMenu[0]);
    } else {
      setRecommendedMenu(null)
    }
  }, [filteredMenu])

  useEffect(() => {
    if (_.isNumber(tableIndex)) {
      if (tableIndex < filteredMenu.length) {
        setRecommendedMenu(filteredMenu[tableIndex]);
      }
    }
  }, [tableIndex, filteredMenu])

  return (
    <Layout>
      <Container className={classes.root}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            <Typography variant="h5">
              Waiter Recommendation
            </Typography>
          </Grid>
          {
            _.isObject(recommendedMenu)
            ? <Grid item xs={12} md={6}>
                <WaiterRecommendationCard tagsDictionary={tagsDictionary} menuObject={recommendedMenu}/>
              </Grid>
            : null
          }
          <Grid item xs={12} md={6}>
            <Box mb={4}>
              {/* Autocomplete for Inclusion*/}
              <Box mb={2}>
                <Autocomplete multiple
                  options={tagsDictionary}
                  onChange={(e, inputTags) => {setInclude(extractIDFromObjectArray(inputTags))}}
                  getOptionLabel={tag => tag.namaKategori}
                  renderInput={params => (
                    <TextField {...params} size="small" labels="Include Tags" fullWidth
                      InputLabelProps={{shrink: true}}
                      placeholder="Include Tags..."
                      helperText="Pilih kategori yang dimiliki oleh menu"
                    />
                  )}
                />
              </Box>

              {/* Autocomplete for Exclusion*/}
              <Box mb={2}>
                <Autocomplete multiple
                  options={tagsDictionary}
                  onChange={(e, inputTags) => {setExclude(extractIDFromObjectArray(inputTags))}}
                  getOptionLabel={tag => tag.namaKategori}
                  renderInput={params => (
                    <TextField {...params} size="small" labels="Exclude Tags" fullWidth
                      InputLabelProps={{shrink: true}}
                      placeholder="Exclude Tags..."
                      helperText="Pilih kategori yang tidak boleh dimiliki oleh menu"
                    />
                  )}
                />
              </Box>

              {/* Autocompletion Search Button */}
              <Button
                disabled={running}
                color="primary"
                variant="contained"
                fullWidth
                onClick={function(event) {
                  // Inclusion filter
                  setRunning(true)
                  let filter1 = (
                    include.length === 0 ? menu :
                    menu.filter(item => {
                      return _.intersection(item.tags, include).length > 0
                    })
                  )
                  let result = filter1.filter(item => {
                    return _.intersection(item.tags, exclude).length === 0
                  })
                  setFilteredMenu(result)
                  setRunning(false)
                }}
              >
                <Typography variant="body" component="b">
                  {!running ? "Berikan Rekomendasi" : <CircularProgress className={classes.spinner} size={14}/>}
                </Typography>
              </Button>
            </Box>

            {
              filteredMenu.length > 0
              ? <WaiterRecommendationTable
                  filteredMenu={filteredMenu}
                  tagsDictionary={tagsDictionary}
                  setters={{
                    setTableIndex
                  }}  
                />
              : null
            }
          </Grid>
        </Grid>
      </Container>
    </Layout>
  )
}

export default WaiterMainPage;