import { toast } from 'react-toastify';
import toastConfig from '../../common/constants/toastConfig';

var axios = require('axios');
const { default: APP_CONSTANTS } = require('../../common/constants/APP_CONSTANTS');

function tagsGetAll(successFunction, errorFunction) {
  axios({
    method: 'get',
    url: `${APP_CONSTANTS.API_URL}/tags`,
    headers: { }
  })
  .then(successFunction)
  .catch(errorFunction);
}

function tagsGetAllHandler(setter, throttleHandler = null) {
  return function (event = null) {
    if (throttleHandler !== null) { throttleHandler(true); }
    tagsGetAll(
      function(res) {
        setter(res.data);
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.success("Daftar tags berhasil diperoleh kembali", toastConfig)
        }
      },
      function(err) {
        console.log(err);
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.error("Maaf, daftar tags gagal diperoleh...", toastConfig)
        }
      }
    )
  }
}

export default tagsGetAllHandler;
