import { Box, Button, CircularProgress, Dialog, DialogActions, DialogContent, DialogTitle, makeStyles, TextField, Typography } from '@material-ui/core';
import _ from 'lodash';
import React, { Fragment, useEffect, useState } from 'react';
import APP_CONSTANTS from '../../common/constants/APP_CONSTANTS';
import { tagsUpdateHandler } from "../../axiosCalls/Tags/tagsUpdate";

const useStyles = makeStyles((theme) => ({
  root: {},
  spinner: {
    color:"white",
  }
}))

function TagsDialogEdit(props) {
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const [name, setName] = useState("");
  const [recommendation, setRecommendation] = useState("Bakmi Ayam");
  const [running, setRunning] = useState(false);
  const [validation, setValidation] = useState(false);

  let {tagsDictionary, tagObject, setters} = props;

  useEffect(() => {
    setRecommendation(APP_CONSTANTS.TAGS_SUGGESTIONS[_.random(0, APP_CONSTANTS.TAGS_SUGGESTIONS.length - 1)]);
    setName(tagObject?.namaKategori || "");

  }, [tagObject, open])

  useEffect(() => {
    let temp = tagsDictionary.map(tag => tag.namaKategori)
    temp = temp.filter(tag => (tag !== tagObject.namaKategori)); 
    let valTemp = temp.findIndex(tag => tag === name.toLowerCase());
    setValidation(valTemp !== -1);
  }, [tagsDictionary, tagObject, name])
  
  const handleOpen = (state) => {
    setOpen(state);
  }

  return (
    <Fragment>
      <Button variant="contained" color="primary" onClick={function() {handleOpen(true)}} size="small">
        <Typography variant="caption">Edit</Typography>
      </Button>
      <Dialog open={open} onClose={function() {handleOpen(false)}}>
        <DialogTitle>
          <Box p={2}>
            Edit {name || "Tag Item"}
          </Box>
        </DialogTitle>
        <DialogContent>
          <Box px={2} mb={4}>
            <TextField label="Nama Item" type="text" size="small" fullWidth
              error={validation}
              placeholder={recommendation}
              InputLabelProps={{shrink: true}}
              helperText={
                validation
                ? `Maaf, kategori ${name} sudah ada di daftar kategori / tags...`
                : "Tambahkan sebuah tag (kategori) yang mendeskripsikan sifat makanan / minuman."
              }
              defaultValue={name}
              onChange={(event) => {setName(event.target.value)}}
            />
          </Box>
        </DialogContent>

        <DialogActions>
          <Box p={2} display="flex" flexDirection="row">
            <Box mr={2}>
              <Button onClick={function() {handleOpen(false)}} color="primary">
                Cancel
              </Button>
            </Box>
            <Button 
              disabled={validation}
              onClick={function(event) {
                handleOpen(false);
                (tagsUpdateHandler(
                  {
                    id: tagObject._id,
                    namaKategori: name,
                  },
                  setRunning,
                  setters.setTagsDictionary
                ))(event);
              }} 
              color="primary" 
              variant="contained"
            >
              {!running ? "Save Changes" : <CircularProgress className={classes.spinner} size={14}/>}
            </Button>
          </Box>
        </DialogActions>
      </Dialog>
    </Fragment>
  )
}

export default TagsDialogEdit;