import { Button, Box, Grid, Typography, makeStyles, Container, TableContainer, Paper, Table, TableHead, TableRow, TableCell, TextField, CircularProgress } from '@material-ui/core';
import React, { useEffect, useState } from 'react';
import menuGetAllHandler from '../../axiosCalls/Menu/menuGetAll';
import tagsGetAllHandler from '../../axiosCalls/Tags/tagsGetAll';
import Layout from '../../common/Layout';
import MenuDialogAdd from './_MenuDialogAdd';
import MenuTableBody from './_MenuTableBody';

const useStyles = makeStyles((theme) => ({
  root: {},
  spinner: {
    color:"white",
  }
}))

function MenuMainPage(props) {
  const [tagsDictionary, setTagsDictionary] = useState([]);
  const [menu, setMenu] = useState([]);
  const [search, setSearch] = useState("");
  const [running, setRunning] = useState(false);

  const classes = useStyles();

  useEffect(() => {
    (async function () {
      await tagsGetAllHandler(setTagsDictionary)();
      await menuGetAllHandler(setMenu, setRunning)();
    })();
  }, [])

  return (
    <Layout>
      <Container>
        <Grid container spacing={2}>
          <Grid item xs={9}>
            <Typography variant="h5" className={classes.root}>
              Menu Management
            </Typography>
          </Grid>
          <Grid item xs={3}>
            <Box display="flex" justifyContent="flex-end">
              <Button disabled={running} size="small" color="primary" variant="contained" onClick={menuGetAllHandler(setMenu, setRunning)}>
                <Typography variant="caption">
                  {!running ? "Refresh" : <CircularProgress className={classes.spinner} size={12}/>}
                </Typography>
              </Button>
            </Box>
          </Grid>
          <Grid item xs={12} md={6}>
            <Typography variant="body1">
              Disini, Anda dapat mengatur item makanan / minuman yang akan direkomendasikan. Jangan lupa
              untuk memberikan detail rekomendasi <i>serving size</i>, harga, dan tags / kategori-kategori
              makanan yang sesuai ya!
            </Typography>
          </Grid>
        </Grid>

        <Box mt={8}>
          <Grid container>
            <Grid item xs={12} md={6}>
              <Typography variant="h6">
                Manage Menu Item
              </Typography>
              <Box mt={1}>
                <TextField label="Search Menu Item by Name" type="text" size="small" fullWidth
                  onChange={(event) => {setSearch(event.target.value)}}
                />
              </Box>
            </Grid>
          </Grid>
        </Box>
        
        <Box mt={3}>
          <TableContainer component={Paper}>
            <Table>
              <TableHead>
                <TableRow>
                  <TableCell padding="checkbox"></TableCell>
                  <TableCell>Name</TableCell>
                  <TableCell>Tags</TableCell>
                  <TableCell>Harga (Rp)</TableCell>
                  <TableCell>Size</TableCell>
                  <TableCell align="right">
                    <MenuDialogAdd 
                      tagsDictionary={tagsDictionary} 
                      setters={{
                        setMenu,
                        setTagsDictionary,
                        throttleHandler: setRunning
                      }}
                    />
                  </TableCell>
                </TableRow>
              </TableHead>
              <MenuTableBody 
                menu={menu} 
                tagsDictionary={tagsDictionary} 
                menuFilter={search} 
                setters={{
                  setMenu,
                  setTagsDictionary,
                  throttleHandler: setRunning
                }}
              />
            </Table>
          </TableContainer>
        </Box>
      </Container>
    </Layout>
  )
}

export default MenuMainPage;