import qs from 'qs';
import { toast } from 'react-toastify';
import toastConfig from '../../common/constants/toastConfig';
import tagsGetAllHandler from './tagsGetAll';

var axios = require('axios');
const { default: APP_CONSTANTS } = require('../../common/constants/APP_CONSTANTS');

function tagsDelete(successFunction, errorFunction, id) {
  axios({
    method: 'delete',
    url: `${APP_CONSTANTS.API_URL}/tags`,
    headers: { 
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    data : qs.stringify({id: id})
  })
  .then(successFunction)
  .catch(errorFunction);
}

function tagsDeleteHandler(id, throttleHandler = null, tagsSetter = null) {
  return function (event = null) {
    console.log("TagsDelete")
    if (throttleHandler !== null) {throttleHandler(true);}
    tagsDelete(
      function(res) {
        // Refresh Tags
        if (tagsSetter !== null) {
          (tagsGetAllHandler(tagsSetter))()
        }

        // Throttler Handler
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.success("Tag berhasil dihapus", toastConfig)
        }
      },
      function(err) {
        console.log(err);
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.error("Maaf, tidak bisa menghapus data. Terjadi kesalahan pada server...", toastConfig)
        }
      },
      id
    )
  }
}

export {tagsDelete, tagsDeleteHandler};