import { toast } from 'react-toastify';
import toastConfig from '../../common/constants/toastConfig';

var axios = require('axios');
const { default: APP_CONSTANTS } = require('../../common/constants/APP_CONSTANTS');

function menuGetAll(successFunction, errorFunction) {
  axios({
    method: 'get',
    url: `${APP_CONSTANTS.API_URL}/menus`,
    headers: { }
  })
  // .then(successFunction)
  .then(successFunction)
  .catch(errorFunction);
}

function menuGetAllHandler(setter, throttleHandler = null) {
  return function (event = null) {
    console.log("MenuGetAllHandler");

    if (throttleHandler !== null) {throttleHandler(true);}
    menuGetAll(
      function(res) {
        console.log(res.data);
        setter(res.data);
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.success("Daftar menu berhasil diperoleh kembali", toastConfig)
        }
      },
      function(err) {
        console.log(err);
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.error("Maaf, daftar menu gagal diperoleh...", toastConfig)
        }
      }
    )
  }
}

export default menuGetAllHandler;
