// tagID = input tag ID
// tagsDictionary = tags dictionary from the server

function tagIDToTagObject(tagID, tagsDictionary) {
  let idx = tagsDictionary.findIndex(elmt => {return tagID === elmt._id})
  if (idx !== -1) {
    return tagsDictionary[idx]
  } else return null;
}

export default tagIDToTagObject;