import qs from 'qs';
import { toast } from 'react-toastify';
import toastConfig from '../../common/constants/toastConfig';
import menuGetAllHandler from './menuGetAll';

var axios = require('axios');
const { default: APP_CONSTANTS } = require('../../common/constants/APP_CONSTANTS');

function menuPost(successFunction, errorFunction, data) {
  axios({
    method: 'post',
    url: `${APP_CONSTANTS.API_URL}/menus`,
    headers: { 
      'Content-Type': 'application/x-www-form-urlencoded'
    },
    data : qs.stringify(data)
  })
  .then(successFunction)
  .catch(errorFunction);
}

function menuPostHandler(data, throttleHandler = null, menuSetter = null) {
  return function(event = null) {
    console.log("MenuPostHandler");
    if (throttleHandler !== null) {throttleHandler(true);}
    menuPost(
      function(res) {
        // Refresh Menu
        if (menuSetter !== null) {
          (menuGetAllHandler(menuSetter))()
        }

        // Throttler Handler
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.success(`Berhasil menamabahkan ${data.name} pada menu`, toastConfig)
        }
      },
      function(err) {
        console.log(err);
        if (throttleHandler !== null) {
          throttleHandler(false);
          toast.dismiss();
          toast.error("Maaf, tidak bisa menambahkan data. Terjadi kesalahan pada server...", toastConfig)
        }
      },
      data
    )
  }
}

export {menuPost, menuPostHandler};
