import React from "react";
import {
  Routes,
  Route,
} from "react-router-dom";
import MenuMainPage from "./Management/Menu/MainPage";
import TagsMainPage from "./Management/Tags/MainPage";

import { createTheme, ThemeProvider } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import { deepOrange, orange } from "@material-ui/core/colors";
import { ToastContainer } from "react-toastify";

import 'react-toastify/dist/ReactToastify.css';
import WaiterMainPage from "./Waiter/MainPage";

const darkTheme = createTheme({
  palette: {
    type: 'dark',
    primary: deepOrange,
    secondary: orange
  },
});

function App() {
  return (
    <ThemeProvider theme={darkTheme}>
      <CssBaseline/>
      <Routes>
        <Route exact path="/" element={<WaiterMainPage/>}/>
        <Route exact path="/management-menu" element={<MenuMainPage />}/>
        <Route exact path="/management-tags" element={<TagsMainPage />}/>
      </Routes>
      <ToastContainer 
        limit={1}
        position="bottom-left"
        autoClose={3000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />
    </ThemeProvider>
  );
}

export default App;
