import { Box, Button, CircularProgress, Dialog, DialogActions, DialogContent, DialogTitle, makeStyles, Typography } from "@material-ui/core"
import { Fragment, useEffect, useState } from "react"
import { menuDeleteHandler } from "../../axiosCalls/Menu/menuDelete";
import DelButton from "../../common/DelButton"

const useStyles = makeStyles((theme) => ({
  root: {},
  spinner: {
    color:"white",
  }
}))

function MenuDialogDelete(props) {
  const classes = useStyles();
  
  const [open, setOpen] = useState(false);
  const [running, setRunning] = useState(false);
  
  const {menuObject, setters} = props
  
  useEffect(() => {
    
  }, [])

  const handleOpen = (state) => {
    setOpen(state);
  }

  return (
    <Fragment>
      <DelButton variant="contained" onClick={function() {handleOpen(true)}} size="small">
        <Typography variant="caption">Delete</Typography>
      </DelButton>

      <Dialog open={open} onClose={function() {handleOpen(false)}}>
        <DialogTitle>
          <Box p={2}>
            Konfirmasi Menghapus {menuObject?.name || "Item Menu"}
          </Box>
        </DialogTitle>
        <DialogContent>
          <Box px={2} mb={4}>
            <Typography>
              Apakah Anda yakin ingin menghapus {menuObject?.name || "item menu ini"}? 
              Sekalinya terhapus, <b>Anda tidak bisa mengulangi operasi ini!</b>
            </Typography>
          </Box>
        </DialogContent>
        <DialogActions>
          <Box p={2} display="flex" flexDirection="row">
            <Box mr={2}>
              <Button onClick={function() {handleOpen(false)}} color="primary">
                Cancel
              </Button>
            </Box>
            <Button onClick={
              function(event) {
                handleOpen(false);
                (menuDeleteHandler(menuObject._id, setRunning, setters.setMenu))(event);
              }
            } color="primary" variant="contained">
              {!running ? "Hapus Item" : <CircularProgress className={classes.spinner} size={14}/>}
            </Button>
          </Box>
        </DialogActions>
      </Dialog>
    </Fragment>
  )
}

export default MenuDialogDelete;