import React, {useState, useEffect} from 'react';
import { makeStyles, TableRow, TableCell, TableBody } from '@material-ui/core';
import TagsDialogEdit from './_TagsDialogEdit';
import TagsDialogDelete from './_TagsDialogDelete';

const useStyles = makeStyles((theme) => ({
  root: {},
}))

function TagsTableBody(props) {
  const [filteredTags, setFilteredTags] = useState([]);
  
  const {tagsDictionary, tagsFilter, setters} = props
  const classes = useStyles();

  useEffect(() => {
    let temp = tagsDictionary;
    let tempFilter = tagsFilter.toLowerCase();

    if (!tempFilter) {
      setFilteredTags(tagsDictionary)
    }

    if (tempFilter) {
      temp = temp.filter(tag => tag.namaKategori.toLowerCase().indexOf(tempFilter) > -1);
      setFilteredTags(temp);
    }

  }, [tagsDictionary, tagsFilter])

  return (
    <TableBody className={classes.root}>
      {
        filteredTags.length > 0
        ? <>
          {filteredTags.map(row => (
            <TableRow key={row._id}>
              <TableCell>{row.namaKategori}</TableCell>
              <TableCell align="right">
                <TagsDialogEdit tagsDictionary={tagsDictionary} tagObject={row} setters={setters}/>
                <TagsDialogDelete tagsDictionary={tagsDictionary} tagObject={row} setters={setters}/>
              </TableCell>
            </TableRow>
          ))}
          </>
        : <TableRow>
            <TableCell colSpan={2} align="center">Tidak ada tag / kategori</TableCell>
          </TableRow> 
      }
    </TableBody>
  )
}

export default TagsTableBody